.descriptionBlock {
    float: right;
    background-color: #FFFFFF;

    border-radius: 4px;
    box-shadow: 0 1px 0 0 #dce1e6, 0 0 0 1px #e7e8ec;

    margin-left: 13px;
    width: 100%;
    padding: 5px;
}

.profileStatus {
    padding: 0 0 15px;
    border-bottom: 1px solid #dce1e6;
}

.profileDataBLock {
    padding-top: 10px;
    font-size: 13px;
    line-height: 150%;
}

.profileDataItem {
    display: flex;
    width: 365px;
}

.profileDataItemKey {
    color: #818c99;
    font-weight: 400;
    width: 165px;
}

.profileDataItemValue {
    color: #2a5885;
}

.userName {
    margin-left: 10px;
}

.userName h3 {
    margin: auto;
    margin-top: 15px;
}

.userName textarea {
    resize: none;
    outline: none;
    border: none;
    width: 100%;
}

.userInfo {
    margin-bottom: 15px;
}
