.addPostContainer {
    background-color: #FFFFFF;
    margin-top: 20px;
    border-radius: 4px;
    box-shadow: 0 1px 0 0 #dce1e6, 0 0 0 1px #e7e8ec;
}

.fieldContainer {
    height: 80px;
    padding: 16px;
}

.fieldUserImg {
    float: left;
}

.postField {
    margin-left: 10px;
}

.buttonContainer {
    border-top: 1px solid #dce1e6;
    padding: 10px;
    height: 34px;
}

.addPostContainer button {
    border-radius: 4px;
    padding: 0 16px;
    float: right;
    width: 130px;
    height: 30px;
    color: white;
    background-color: #5181b8;
    border: none;
    outline: none;
    overflow: hidden;
    cursor: pointer;
}

.addPostContainer button:hover {
    opacity: 0.8;
}

.addPostContainer textarea {
    border: none;
    resize: none;
    outline: none;
}
