.profileImageBlock {
    float: left;
    background-color: white;
    width: 280px;
    height: 260px;
    border-radius: 4px;
    box-shadow: 0 1px 0 0 #dce1e6, 0 0 0 1px #e7e8ec;
    padding: 15px 15px 6px 15px;
}

.pagePhoto {

}

.userImgContainer img {
    height: 200px;
    width: 200px;
}

.editButton button {
    background-color: #e5ebf1;
    color: #55677d;
    border: none;
    outline: none;
    margin-top: 10px;
    width: 200px;
    height: 30px;
    cursor: pointer;
    border-radius: 4px;
}

.editButton button:hover {
    background-color: #dde1e9;
}

input[type=file] {
    display: none;
}

.customFileUpload {
    display: inline-block;
    cursor: pointer;
}
